
const LoginInfo = 'LoginInfo'

class Util {
  static getLoginInfo () {
    let loginInfo = sessionStorage.getItem(LoginInfo)
    if (loginInfo) {
      try {
        return JSON.parse(loginInfo)
      } catch (e) {

      }
    }
    return {}
  }

  static isLogined () {
    let token = Util.getToken()
    if (token && token !== '') {
      return true
    }
    return false
  }

  static setLoginInfo (loginInfo) {
    sessionStorage.setItem(LoginInfo, JSON.stringify(loginInfo))
  }

  static getToken () {
    let loginInfo = Util.getLoginInfo()
    if (loginInfo) {
      return loginInfo.Token
    }
    return ''
  }

  static _setSubDir (dirs, dirMap) {
    if (dirs) {
      for (let i in dirs) {
        let dir = dirs[i]
        let subDirs = dirMap[dir.ID]
        if (subDirs && subDirs instanceof Array) {
          dir.SubDirs = subDirs
          Util._setSubDir(subDirs, dirMap)
        }
      }
    }
  }

  static preparedDir (allDirs) {
    let dirMap = {}
    for (let i in allDirs) {
      let dir = allDirs[i]
      let pId = dir.PID
      if (!pId || pId === null || pId === '') {
        pId = 'root'
      }
      let dirArray = dirMap[pId]
      if (!dirArray) {
        dirArray = []
      }
      dir.TempName = dir.DirName
      dirArray.push(dir)
      dirMap[pId] = dirArray
    }
    let rootDirs = dirMap['root']
    if (!rootDirs) {
      rootDirs = []
    }
    Util._setSubDir(rootDirs, dirMap)
    return rootDirs
  }

  static loadNav (vue) {
    vue.$axios.post(vue.$API.DIR_LIST).then(response => {
      if (vue.$response.Code.Success === response.Code) {
        vue.$store.state.allDirs = response.Data
        vue.$store.commit('allDirs')
      } else {
        console.log(response.Message)
      }
    }).catch(err => {
      console.log(err)
    })
  }

  static getDir (vue, id) {
    var allDirs = vue.$store.state.allDirs

    if (!id) {
      return {DirName: vue.$t('目录'), SubDirs: allDirs}
    }

    if (allDirs && allDirs.length > 0) {
      for (let i in allDirs) {
        if (id === allDirs[i].ID) {
          return allDirs[i]
        }
      }
    }
    vue.$router.push('/')
    document.title = vue.$t('title')
    return {}
  }

  static changeLocale (vue, locale) {
    vue.$i18n.locale = locale
    vue.$store.state.locale = locale
    vue.$store.commit('locale')
    Util.loadNav(vue)
    document.title = vue.$t('title')
  }
}

export default Util



