<template>
  <div class="main-container">
    <div class="left-container">
      <ul class="dir-tree">
        <item-tree
          class="item"
          :model="model" v-on:loadArticle="loadArticle">

        </item-tree>
      </ul>
    </div>
    <div class="right-container">
      <div class="right-innter-container">
        <div v-if="'' != articleDirId">
          <div class="row">
            <div class="col-3 border-bottom">
              <b-button size="md" @click="addArticle" class="btn btn-outline-info" v-if="$store.state.isLogined">
                +
              </b-button>
              <div class="article-item" :class="index == activeIndex ? 'active' : ''" v-for="(art, index) in articleList" :key="index">
                <a href="javascript:;" @click="loadArticleContent(art, index)">
                  {{art.Title}}
                  <span v-if="$store.state.isLogined" @click.stop class="btn-art-opt"><a @click="deleteArticle(art, index)"><i class="fa fa-trash" v-b-tooltip :title="$t('删除')" aria-hidden="true"></i></a></span>
                </a>
              </div>
            </div>
            <div class="col-9">
              <Article :article="article" v-if="null != article" />
            </div>
          </div>
        </div>
      </div>
      <div>
        <router-view />
      </div>
      <b-modal centered id="modalArticleDelete"
             ref="modalArticleDelete"
             :title="$t('确认')"
             @ok="doDeleteArticle" :cancel-title="$t('取消')" :ok-title="$t('确定')" lazy>
        <p class="modal-pannel">[ {{deleteObject.article ? deleteObject.article.Title : ''}} ] <span>{{$t('文章将要被删除，请确认')}}</span></p>
      </b-modal>
    </div>
  </div>
</template>

<script>
import Article from './Article'
export default {
  name: 'Dir',
  components: {
    Article
  },
  data () {
    return {
      articleList: [],
      articleDirId: '',
      article: null,
      activeIndex: null,
      deleteObject: Object
    }
  },
  mounted () {
    this.loadArticle(this.articleDirId)
  },
  computed: {
    model () {
      return this.$Util.getDir(this, this.$route.params.id)
    }
  },
  methods: {
    loadArticle (dirId) {
      this.articleDirId = dirId
      let that = this
      this.$axios.post(this.$API.ART_LIST, JSON.stringify({
        DirID: this.articleDirId,
        Page: 1,
        Size: 9999
      })).then(response => {
        if (that.$response.Code.Success === response.Code) {
          that.articleList = response.Data.Content
        } else {
          that.errorMessage = response.Message
        }
      }).catch(err => {
        that.errorMessage = err.Error
      })
    },
    addArticle () {
      let that = this
      this.$axios.post(this.$API.ART_ADD, JSON.stringify({
        DirID: this.articleDirId,
        Title: this.$t('未命名'),
        Content: '## ' + this.$t('未命名')
      })).then(response => {
        if (that.$response.Code.Success === response.Code) {
          that.articleList.push(response.Data)
        } else {
          that.errorMessage = response.Message
        }
      }).catch(err => {
        that.errorMessage = err.Error
      })
    },
    loadArticleContent (article, activeIndex) {
      this.article = article
      this.activeIndex = activeIndex
    },
    deleteArticle (article, index) {
      this.deleteObject = {
        article: article,
        index: index
      }
      console.log(this.deleteObject)
      this.$refs['modalArticleDelete'].show()
    },
    doDeleteArticle () {
      let that = this
      this.$axios.post(this.$API.ART_REMOVE, JSON.stringify({
        IDS: [this.deleteObject.article.ID]
      })).then(response => {
        if (that.$response.Code.Success === response.Code) {
          if (that.article.ID === that.deleteObject.article.ID) {
            that.article = null
          }
          that.articleList.splice(that.deleteObject.index, 1)
        } else {
          that.errorMessage = response.Message
        }
      }).catch(err => {
        that.errorMessage = err.Error
      })
    }
  },
  watch: {
    articleDirId (value) {
      console.log('load -> ' + value)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .right-innter-container {
    position: relative;
    background-color: #fff;
  }
  .v-note-wrapper.markdown-body {
    min-height: 50rem;
  }

  .article-item a {
    position: relative;
    color: #17a2b8;
    text-decoration: none;
    padding: .7rem;
    line-height: .9rem;
    width: 100%;
    display: block;
  }

  .article-item a:hover {
    background-color: #ccc;
  }

  .article-item.active, .article-item.active a:hover {
    background-color: #17a2b8;
  }

  .article-item.active a {
    color: #fff;
  }

  .btn {
    border-radius: 0;
    width: 9rem;
    padding: 0 0;
    margin: .5rem;
    line-height: .9rem;
    height: 2rem;
  }

  .btn-outline-info, .btn-outline-info:not(:disabled):not(.disabled).active, .btn-outline-info:not(:disabled):not(.disabled):active, .show>.btn-outline-info.dropdown-toggle {
    border-style: dashed;
  }
  .btn-outline-info.focus, .btn-outline-info:focus {
    box-shadow: none;
  }

  .row {
    margin: 0;
  }

  .col-3 {
    flex: 0 0 10rem;
    width: 10rem;
    background-color: #fafafa;
  }

  .row .col-3, .row .col-9 {
    padding: 0;
  }

  .col-9 {
    flex: 0 0 calc(100% - 10rem);
    max-width: calc(100% - 10rem);
  }

  .btn-art-opt {
    float: right;
    position: absolute;
    top: 0;
    right: 0.25rem;
  }
</style>



