<template>
  <div class="c-nav c-color-nav fixed-top">
    <b-navbar toggleable="md" type="dark" variant="dark">

    <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

    <b-navbar-brand href="#/">
      <img class="logo" src="../assets/images/logo.png" alt="">
      <span class="brand" :title="$t('title')">{{$t("title")}}</span>
    </b-navbar-brand>

    <b-collapse is-nav id="nav_collapse">

      <b-navbar-nav>
        <li class="nav-item" v-for="(dir, index) in allDirs" :key="dir.ID">
          <router-link v-if="index < 5" :to="'/dir/' + dir.ID" class="nav-bg nav-link">
            {{dir.DirName}}
          </router-link>
        </li>
        <b-nav-item-dropdown right v-if="allDirs.length > 5">
          <template slot="button-content">
            {{$t('其他')}}
            <i class="fa fa-angle-down"></i>
          </template>
          <router-link class="dropdown-item" v-for="(dir, index) in allDirs" :key="dir.ID" v-if="index >= 5"  :to="'/dir/' + dir.ID">
            {{dir.DirName}}
          </router-link>
        </b-nav-item-dropdown>
        <li class="nav-item" v-if="$store.state.isLogined">
          <b-button @click="addRootDir" size="md" class="btn btn-outline-white">
            +
          </b-button>
          <b-modal centered id="modalPrevent"
              ref="modalPrevent"
              :title="$t('请输入目录名称')"
              @ok="handleOk" :cancel-title="$t('取消')" :ok-title="$t('确定')">
            <form @submit.stop.prevent="handleSubmit">
              <b-form-input type="text"
                            :placeholder="$t('请输入目录名称')"
                            v-model="model.TempName"></b-form-input>
            </form>
          </b-modal>
        </li>
      </b-navbar-nav>

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto">

        <li class="nav-item">
          <b-nav-item-dropdown v-if="isLogined" right>
            <template slot="button-content">
              <i class="fa fa-user"></i>
              &nbsp;
              {{loginInfo.Nickname}}
            </template>
            <b-dropdown-item @click="logout()">
              <i class="fa fa-sign-out"></i>
              &nbsp;
              {{$t('退出登录')}}
            </b-dropdown-item>
          </b-nav-item-dropdown>
          <router-link v-if="!isLogined" to="/login" class="nav-link">
            {{$t("登录")}}
          </router-link>
        </li>

        <b-nav-item-dropdown right>
          <template slot="button-content">
            <img :src="$t('langPng')" :alt="$t('langName')">
            <i class="fa fa-angle-down"></i>
          </template>
          <b-dropdown-item @click="changeLang('zh-CN')">
            <img src="../assets/images/zh-CN.png" alt="简体中文" />
            简体中文
          </b-dropdown-item>
          <b-dropdown-item @click="changeLang('en')">
            <img src="../assets/images/en.png" alt="English" />
            English
          </b-dropdown-item>
        </b-nav-item-dropdown>

      </b-navbar-nav>

    </b-collapse>
  </b-navbar>
  <div class="clear"></div>
</div>
</template>

<script>
export default {
  name: 'Nav',
  data () {
    return {
      model: Object
    }
  },
  methods: {
    changeLang: function (lang) {
      this.$Util.changeLocale(this, lang)
    },
    logout: function () {
      // TODO send ajax
      this.$store.state.loginInfo = {}
      this.$store.commit('loginInfo')
    },
    addRootDir () {
      this.$refs['modalPrevent'].show()
    },
    handleOk (evt) {
      evt.preventDefault()
      if (!this.model.TempName) {
        this.$notice('Please enter your name')
      } else {
        this.handleSubmit()
      }
    },
    handleSubmit () {
      let that = this
      this.$axios.post(this.$API.DIR_ADD, JSON.stringify({
        DirName: this.model.TempName,
        SerNo: 0,
        Lang: this.$store.state.locale
      })).then(response => {
        if (that.$response.Code.Success === response.Code) {
          that.$Util.loadNav(that)
        } else {
          that.errorMessage = response.Message
        }
        that.$refs['modalPrevent'].hide()
      }).catch(err => {
        that.errorMessage = err.Error
        that.$refs['modalPrevent'].hide()
      })
    }
  },
  mounted () {
    document.title = this.$t('title')
    this.$Util.loadNav(this)
  },
  computed: {
    loginInfo () {
      return this.$store.state.loginInfo
    },
    isLogined () {
      return this.$store.state.isLogined
    },
    allDirs () {
      return this.$store.state.allDirs
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .logo {
    width: 2rem;
    height: 2rem;
  }
  .brand {
    font-size: 80%;
  }
  .navbar-brand {
    width: 13rem;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .c-color-nav {
    color: #ffffff;
  }

  .btn {
    border-radius: 0;
    width: 1rem;
    padding: 0 0;
    margin-left: .5rem;
    line-height: .9rem;
    height: 1rem;
    margin-top: 1.4rem;
  }

  .btn-outline-white {
    color: #fff;
    background-color: transparent;
    background-image: none;
    border: 1px dashed #fff;
  }

  .btn-outline-white:hover {
    background-color: rgba(255, 255, 255, .1);
  }

  .btn-outline-white:not(:disabled):not(.disabled).active, .btn-outline-white:not(:disabled):not(.disabled):active, .show>.btn-outline-white.dropdown-toggle {
    background-color: rgba(255, 255, 255, .1);
    border: 1px dashed #fff;
  }

  .btn-outline-white.focus, .btn-outline-white:focus {
    box-shadow: none;
  }
</style>



