import Vue from 'vue'

const NoticeConstructor = Vue.extend(require('./Notice.vue'))

let noticeId = 1

const Notice = (content) => {
  let id = 'notice-' + (noticeId++)
  const NoticeInstance = new NoticeConstructor({
    data: {
      content: content
    }
  })

  NoticeInstance.id = id
  NoticeInstance.vm = NoticeInstance.$mount()
  NoticeInstance.vm.visible = true
  NoticeInstance.dom = NoticeInstance.vm.$el
  console.log(NoticeInstance.vm.dom)
  document.body.appendChild(NoticeInstance.dom)
  NoticeInstance.dom.style.zIndex = noticeId + 1001
  return NoticeInstance.vm
}

export default {
  install: Vue => {
    Vue.prototype.$notice = Notice
  }
}



