// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import i18n from './assets/js/i18n'
import BootstrapVue from 'bootstrap-vue'
import Vuex from 'vuex'
import axios from 'axios'

import Util from './assets/js/util'
import API from './assets/js/api'
import '@/components/Tree'
import Notice from './common/notice'
import VueDragDrop from 'vue-drag-drop'
import mavonEditor from 'mavon-editor'

import './assets/js/response'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import 'font-awesome/css/font-awesome.css'
import 'mavon-editor/dist/css/index.css'
import './assets/css/main.css'

Vue.config.productionTip = false

Vue.use(BootstrapVue)
Vue.use(Vuex)
Vue.use(Notice)
Vue.use(VueDragDrop)
Vue.use(mavonEditor)

Vue.prototype.$Util = Util
Vue.prototype.$API = API

const store = new Vuex.Store({
  state: {
    isLogined: Util.isLogined(),
    loginInfo: Util.getLoginInfo(),
    allDirs: [],
    locale: i18n.locale,
    dragData: null
  },
  mutations: {
    loginInfo (state) {
      this.state.loginInfo = state.loginInfo
      Util.setLoginInfo(this.state.loginInfo)
      if (state.loginInfo && state.loginInfo.Token && state.loginInfo.Token !== '') {
        this.state.isLogined = true
      } else {
        this.state.isLogined = false
      }
    },
    allDirs (state) {
      if (state.allDirs && state.allDirs instanceof Array) {
        this.state.allDirs = Util.preparedDir(state.allDirs)
      }
    },
    locale (state) {
      this.state.locale = state.locale
    },
    dragData (state) {
      this.state.dragData = state.dragData
    }
  }
})

axios.interceptors.request.use(function (config) {
  config.headers['Content-type'] = 'application/json;charset=utf-8;'
  let token = Util.getToken()
  if (token && token !== '') {
    config.headers['Token'] = token
  }
  config.headers['Lang'] = store.state.locale
  return config
}, function (error) {
  return Promise.reject(error)
})

axios.interceptors.response.use(response => {
  return response.data
}, (responseError) => {
  console.log(responseError)
})

Vue.prototype.$axios = axios

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  i18n,
  components: { App },
  template: '<App/>'
})



