<template>

    <div class="c-color-footer color-fff">
        <div class="color-wrapper">
            <div class="container">
                <div class="row">
                    <div class="col-md-3">
                        <div>
                            <img class="logo" src="../assets/images/logo.png" alt="">
                        </div>
                    </div>
                    <div class="col-md-9">
                        <div>
                            {{$t('title')}}
                        </div>
                        <div v-html="$t('declaration')"></div>
                    </div>
                </div>
                <hr/>
                <div class="row">
                    <div class="col-md-6 text-left">
                        <div class="c-footer-dropdown">
                            <b-dropdown dropup variant="light" class="m-2">
                                <template slot="button-content" class="color-fff">
                                    <img :src="$t('langPng')" :alt="$t('langName')">
                                    <span class="color-fff">
                                        {{$t('langName')}}
                                        <i class="fa fa-angle-up"></i>
                                    </span>
                                </template>
                                <b-dropdown-item @click="changeLang('zh-CN')">
                                    <img src="../assets/images/zh-CN.png" alt="简体中文" />
                                    简体中文
                                </b-dropdown-item>
                                <b-dropdown-item @click="changeLang('en')">
                                    <img src="../assets/images/en.png" alt="English" />
                                    English
                                </b-dropdown-item>
                            </b-dropdown>
                        </div>
                    </div>
                    <div class="col-md-6 text-right lh-52">
                        <div>
                            &copy; 2018 lq186.com
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
export default {
  name: 'Footer',
  methods: {
    changeLang: function (lang) {
      this.$Util.changeLocale(this, lang)
    }
  }
}
</script>

<style scoped>

    .logo {
        width: 5rem;
        height: 5rem;
    }

    hr {
        border-color: rgba(255, 255, 255, .1);
    }

    .color-wrapper {
        padding: 1rem;
        background-color: rgba(26, 29, 41, .8) !important;
    }
    .color-fff {
        color: #ffffff !important;
        text-decoration: none
    }
    .container {
        margin-top: 1rem;
    }
    .lh-52 {
        line-height: 52px;
    }

    .col-md-3 div, .col-md-9 div {
        margin: 1rem;
    }

    .c-color-footer {
        color: #ffffff;
    }
    @keyframes changebg {
        0%   {background-color:#001f3f;}
        10%  {background-color:#0074D9;}
        20%  {background-color:#7FDBFF;}
        30%  {background-color:#39CCCC;}
        40%  {background-color:#3D9970;}
        50%  {background-color:#2ECC40;}
        60%  {background-color:#01FF70;}
        70%  {background-color:#FFDC00;}
        80%  {background-color:#FF851B;}
        90%  {background-color:#FF4136;}
        100%  {background-color:#B10DC9;}
    }
</style>



