<template>
  <div v-if="'' != model.DirName">
    <li class="item-tree">
      <drop @drop="handleDrop(model)">
      <drag :draggable="$store.state.isLogined" @dragstart="handleDrag" :transfer-data="model">
        <nobr>
        <span class="item" @dblclick="toggle" @click="emitLoadArticle" :class="{'active': isFolder}">
        <!--<span v-if="isFolder">
          <i v-if="open" class="fa fa-minus-square" aria-hidden="true"></i>
          <i v-if="!open" class="fa fa-plus-square" aria-hidden="true"></i>
        </span>-->
        <i class="fa" :id="model.DirName + flag" :class="{'fa-folder-open': open && isFolder, 'fa-folder': !(open && isFolder)}" aria-hidden="true"></i>
        {{model.DirName}}
        </span>
        </nobr>
        <div v-if="$store.state.isLogined" class="btn-tree-opt-group">
          <span @click.stop class="btn-tree-opt"><a @click="addChild"><i class="fa fa-plus-square" aria-hidden="true"></i></a></span>
          <span @click.stop class="btn-tree-opt"><a @click="deleteSelf"><i class="fa fa-minus-square" aria-hidden="true"></i></a></span>
          <span @click.stop class="btn-tree-opt"><a @click="editSelf"><i class="fa fa-pencil-square" aria-hidden="true"></i></a></span>
        </div>
      </drag>
      </drop>
      <ul v-show="open" v-if="isFolder">
        <item-tree
          v-for = "(model, index) in model.SubDirs"
          :key = "index"
          :model = "model" v-on:loadArticle="loadArticle">
        </item-tree>
      </ul>
    </li>
    <b-modal centered :id="'modalPrevent' + this.model.ID"
             :ref="'modal' + this.model.ID"
             :title="$t('请输入目录名称')"
             @ok="handleOk" :cancel-title="$t('取消')" :ok-title="$t('确定')">
      <form @submit.stop.prevent="handleSubmit">
        <b-form-input type="text"
                      :placeholder="$t('请输入目录名称')"
                      v-model="model.TempName"></b-form-input>
      </form>
    </b-modal>
    <b-modal centered :id="'modalDelete' + this.model.ID"
             :ref="'modalDelete' + this.model.ID"
             :title="$t('确认')"
             @ok="removeSelf" :cancel-title="$t('取消')" :ok-title="$t('确定')">
      <p class="modal-pannel">[ {{model.DirName}} ] <span>{{$t('文件夹将要被删除，请确认')}}</span></p>
    </b-modal>
  </div>
</template>

<script type="text/javascript">
import Vue from 'vue'

var Tree = {
  name: 'item-tree',
  props: {
    model: Object
  },
  data: function () {
    return {
      open: true,
      isEdit: false,
      flag: 0,
      errorMessage: ''
    }
  },
  computed: {
    isFolder () {
      return this.model.SubDirs && this.model.SubDirs.length && this.model.SubDirs.length > 0
    },
    dragData () {
      return this.$store.state.dragData
    }
  },
  methods: {
    toggle: function () {
      if (this.isFolder) {
        this.open = !this.open
      }
    },
    emitLoadArticle () {
      this.$emit('loadArticle', this.model.ID)
    },
    addChild: function () {
      this.isEdit = false
      this.$refs['modal' + this.model.ID].show()
    },
    removeChild: function (ID) {
      var subDirs = this.model.SubDirs
      for (var i = 0; i < subDirs.length; ++i) {
        if (ID === subDirs[i].ID) {
          subDirs.splice(i, 1)
          this.model.SubDirs = subDirs
          this.flag += 1
          return
        }
      }
    },
    removeSelf: function () {
      let that = this
      this.$axios.post(this.$API.DIR_REMOVE, JSON.stringify({
        ID: this.model.ID
      })).then(response => {
        if (that.$response.Code.Success === response.Code) {
          if (that.$parent.removeChild) {
            that.$parent.removeChild(that.model.ID)
          } else {
            that.$Util.loadNav(that)
          }
        } else {
          that.errorMessage = response.Message
        }
      }).catch(err => {
        that.errorMessage = err.Error
      })
    },
    editSelf: function () {
      this.isEdit = true
      this.$refs['modal' + this.model.ID].show()
    },
    handleOk (evt) {
      // Prevent modal from closing
      evt.preventDefault()
      if (!this.model.TempName) {
        this.$notice('Please enter your name')
      } else {
        this.handleSubmit()
      }
    },
    deleteSelf () {
      this.$refs['modalDelete' + this.model.ID].show()
    },
    handleSubmit () {
      if (!this.isEdit) {
        let that = this
        this.$axios.post(this.$API.DIR_ADD, JSON.stringify({
          DirName: this.model.TempName,
          PID: this.model.ID,
          SerNo: 0,
          Lang: this.$store.state.locale
        })).then(response => {
          if (that.$response.Code.Success === response.Code) {
            if (!that.model.SubDirs || !(that.model.SubDirs instanceof Array)) {
              Vue.set(that.model, 'SubDirs', [])
            }
            that.open = true
            that.model.SubDirs.push({
              ID: response.Data.ID,
              TempName: response.Data.Name,
              DirName: response.Data.Name,
              PID: response.Data.PID
            })
          } else {
            that.errorMessage = response.Message
          }
          that.flag += 1
          that.$refs['modal' + that.model.ID].hide()
        }).catch(err => {
          that.errorMessage = err.Error
          that.$refs['modal' + that.model.ID].hide()
        })
      } else {
        let that = this
        this.$axios.post(this.$API.DIR_UPDATE, JSON.stringify({
          DirName: this.model.TempName,
          ID: this.model.ID,
          Lang: this.$store.state.locale
        })).then(response => {
          if (that.$response.Code.Success === response.Code) {
            that.model.DirName = that.model.TempName
          } else {
            that.errorMessage = response.Message
          }
          that.flag += 1
          that.$refs['modal' + that.model.ID].hide()
        }).catch(err => {
          that.errorMessage = err.Error
          that.$refs['modal' + that.model.ID].hide()
        })
      }
    },
    handleDrop (data) {
      try {
        let dragData = this.$store.state.dragData
        if (this.canDrag(dragData, data)) {
          dragData.PID = data.ID
          let that = this
          this.$axios.post(this.$API.DIR_UPDATE, JSON.stringify(dragData)).then(response => {
            if (that.$response.Code.Success === response.Code) {
              if (!data.SubDirs || !(data.SubDirs instanceof Array)) {
                Vue.set(data, 'SubDirs', [])
              }
              data.SubDirs.push(this.$store.state.dragData)
            } else {
              that.errorMessage = response.Message
            }
            that.flag += 1
            dragData.$func(dragData.ID)
            that.$store.state.dragData = null
            that.$store.commit('dragData')
          }).catch(err => {
            that.errorMessage = err.Error
            that.$store.state.dragData = null
            that.$store.commit('dragData')
          })
        }
      } catch (e) {
        console.log(e)
        this.$store.state.dragData = null
        this.$store.commit('dragData')
      }
    },
    handleDrag (data, event) {
      data.$func = this.$parent.removeChild
      if (!this.$store.state.dragData) {
        this.$store.state.dragData = data
        this.$store.commit('dragData')
      }
    },
    canDrag (dragData, data) {
      if (dragData.ID === data.ID) {
        return false
      }
      if (dragData.SubDirs && dragData.SubDirs.length) {
        for (var i = 0; i < dragData.SubDirs.length; ++i) {
          return this.canDrag(dragData.SubDirs[i], data)
        }
      }
      return true
    },
    loadArticle (id) {
      this.$emit('loadArticle', id)
    }
  },
  watch: {
    isFolder (value) {
      this.open = value
    }
  }
}

Vue.component('item-tree', Tree)
export default Tree
</script>

<style>
  .modal-pannel {
    color: #333;
    text-align: left;
  }
</style>



