import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Nav from '@/components/Nav'
import Footer from '@/components/Footer'

import Dir from '@/components/Dir'
import Login from '@/components/Login'
import Article from '@/components/Article'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      components: {
        default: Main,
        nav: Nav
      },
      children: [{
        path: '/',
        name: 'Footer',
        component: Footer
      }]
    },
    {
      path: '/dir/:id',
      name: 'Dir',
      components: {
        default: Dir,
        nav: Nav
      },
      children: [{
        path: '/',
        name: 'Footer',
        components: {
          default: Footer,
          article: Article
        }
      }]
    },
    {
      path: '/login',
      name: 'Login',
      components: {
        default: Login,
        nav: Nav
      },
      children: [{
        path: '/',
        name: 'Footer',
        component: Footer
      }]
    }
  ]
})



