<template>
  <div class="notice" id="notice">
    <div class="notice-content">
      {{content}}
    </div>
  </div>
</template>

<script>
export default {
  name: 'notice',
  el: '#notice',
  data () {
    return {
      visible: false,
      content: '',
      duration: 3000
    }
  },
  methods: {
    setTimer () {
      setTimeout(() => {
        this.close()
      }, this.duration)
    },
    close () {
      this.visible = false
      setTimeout(() => {
        this.$destroy(true)
        this.$el.parentNode.removeChild(this.$el)
      }, 500)
    }
  },
  mounted () {
    this.setTimer()
  }
}
</script>



